 }
@keyframes zoomIn {
    0% {
      transform: scale(0.5);
      opacity: 0;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  @keyframes zoomOut {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    100% {
      transform: scale(0.5);
      opacity: 0;
    }
  }
  
  .zoom-in {
    animation: zoomIn 0.5s ease-out forwards;
  }
  
  .zoom-out {
    animation: zoomOut 0.3s ease-out forwards;
  }
  .shadow{
    box-shadow: 7px 7px 15px rgba(0, 0, 0, 0.3);
  }
  .order-popup{
    background: rgb(64,3,3);
    background: linear-gradient(90deg, rgba(64,3,3,1) 30%, rgba(62,60,1,1) 53%, rgba(53,7,71,1) 100%);
color:white;
border:2px solid rgb(9, 255, 0);
box-shadow: 0px 2px 15px rgb(9, 255, 0) ;
  }
 .cancel, .place{
    margin:5px ;
    padding:0px 10px;
}
.cancel{
border-radius: 20px;
   border: 2px solid #1ffc02;
   color: black;
   box-shadow: inset 0 0 10px #1ffc02;
   transition: all 0.5s ease;
}
.cancel:hover{
background-color: #1ffc02;
color: black;
border: 2px solid rgb(104, 14, 14);
}
.place{
    border-radius: 20px;
    background-color: aqua;
       border: 2px solid #fcf802;
       color: black;
       box-shadow: inset 0 0 10px #fcf802;
       transition: all 0.5s ease;
    }
    .place:hover{
    background-color: #fcf802;
    color: black;
    border: 2px solid rgb(104, 14, 14);
    } 
  