.menuClr{
    background-color: rgb(190, 187, 187);
    z-index: 1000;
}
.menuClr h1 {
    cursor: pointer;
    transition: all ease 0.3s; 
    border-width: 0 0 2px; 
    border-color:rgb(190, 187, 187); 
  }
  
  .menuClr h1:hover {
    color: #0250c4; 
    border-color: #0250c4; 
  }