.Track{
    text-align: center;
    font-size: 25px;

    font-style: italic;
  
}
.Track::first-letter{
    color: rgb(255, 0, 0);
    font-size: 35px;
}
.logo{
    border:2px solid black;
    transform: skew(-20deg);

}
.bg1{
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
}
.but:hover{
 box-shadow: 0px 0px 8px blue;
}
.but{
    transition: all 0.5s ease-in-out;
}
.inp{
    background: rgb(150,227,228);
background: linear-gradient(0deg, rgba(150,227,228,1) 0%, rgba(255,221,221,1) 100%);
}

.inp:hover{
    box-shadow:0px 0px 8px red;
    transition: all 0.5s ease;
    background: rgba(255,221,221,1);
    background: linear-gradient(0deg, rgba(255,221,221,1) 0%, rgb(150,227,228) 100%);
}



.add{
    background-color: rgba(255, 255, 255, 0.7); 
    box-shadow: 0px 0px 10px black;
    backdrop-filter: blur(3px);
    height: 100px;
    width:250px;
    border-radius:10px;
}
.mail{
    
    word-break: break-all; 
    overflow-wrap: break-word; 
}
.bottom{
    background-image: url(../Images/pngegg1.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height:250px;
}

@media Screen and (max-width:800px){
.bottom{
    background-image: url(../Images/pngegg.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height:auto;
}
} 
