.background-image {
    height: 100vh; 
    width: 100vw;  
    background-image: url('../Images/Background.jpg');  
    background-size: cover;  
    background-position: center; 
    background-repeat: no-repeat;  
}
.value{
    background-color: #00000077;
}
.spoon{
    color: #02fcef;
    text-shadow: 0 0 5px yellow;
  
}

 .order{
    position: relative;
    border:1px solid #fce302;
    padding: 2px 7px;
    margin-top: 8px;
    color:#fc02c5;
    background-color: black;
    z-index: 1;
    overflow: hidden;
    transition: color 1s,box-shadow 1s;;
}
.order:hover{
    transition-delay: 0s,1s;
    color:#fff;
    box-shadow: 
    0 0 10px #fc02c5,
    0 0 20px #fc02c5,
    0 0 40px #fc02c5,
    0 0 80px #fc02c5,
    0 0 160px #fc02c5;
}
.order::before{
    content: "";
    position: absolute;
    top:0;
    left: -50px;
    width: 0;
    height: 100%;
    background:#fc02c5;
    transform: skewX(35deg);
    z-index: -1;
    transition: 1s;
}
.order:hover:before{
    width: 100%;
} 
 .track{
    position: relative;
    border:1px solid #fc02c5;
    padding: 2px 7px;
    margin-top: 8px;
    color:#fce302;
    background-color: black;
    z-index: 1;
    overflow: hidden;
    transition: color 1s,box-shadow 1s;;
}
.track:hover{
    transition-delay: 0s,1s;
    color:rgb(218, 44, 44);
    box-shadow: 
    0 0 10px #fce302,
    0 0 20px #fce302,
    0 0 40px #fce302,
    0 0 80px #fce302,
    0 0 160px #fce302;
}
.track::before{
    content: "";
    position: absolute;
    top:0;
    left: -50px;
    width: 0;
    height: 100%;
    background:#fce302;
    transform: skewX(35deg);
    z-index: -1;
    transition: 1s;
}
.track:hover:before{
    width: 100%;
} 
.order,
.track {
    z-index: 900; 
} 